import React, { Component } from "react";

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import moment from "moment";

let date = new Date("December 6, 1995 23:15:30");
let weekday = date.getDate();

console.log("DAY", weekday);

const dpStyles = {
  display: "flex",
  flexDirection: "row",
  justifyContent: "space-between",
};

const contentBoxStyles = {
  padding: "0 5px",
  height: "20px",
  display: "flex",
  alignItems: "right",
};

const contentBoxStylesEndDate = {
  padding: "0 0px 0 10px",
  height: "20px",
  display: "flex",
  alignItems: "right",
};

class MyDatepickerComponent extends Component {
  state = {
    startDate: null,
    endDate: null,
    maxEndDate: null,
  };

  handleStartDayChange = (date, event) => {
    // console.log("date, event", date, event);
    const test = moment(date).add(2, "months").toDate();
    console.log("test: ", test);
    this.setState({
      startDate: date,
      endDate: null,
      maxEndDate: test,
    });
  };

  handleEndDayChange = (date, event) => {
    this.setState((oldState) => {
      const $endDate = moment(date).subtract(1, "day");
      const newState = { ...oldState, endDate: $endDate.toDate() };
      console.log("newState", newState);
      this.props.onChange(newState);
      return newState;
    });
  };

  filterDays = (date) => {
    if (date.getDate() !== 1) {
      return false;
    } else {
      return true;
    }
  };

  render() {
    return (
      <div style={dpStyles}>
        <div style={contentBoxStyles}>
          <DatePicker
            selected={this.state.startDate}
            onChange={this.handleStartDayChange}
            dateFormat="dd/MM/yyyy  EE hh:mm a"
            maxDate={new Date()}
            filterDate={this.filterDays}
          />
        </div>
        <span className="poStyles">  </span>

        <div style={contentBoxStylesEndDate}>
          <DatePicker
            selected={this.state.endDate}
            onChange={this.handleEndDayChange}
            dateFormat="dd/MM/yyyy  EE hh:mm a"
            maxDate={this.state.maxEndDate}
            minDate={this.state.startDate}
            filterDate={this.filterDays}
          />
        </div>
      </div>
    );
  }
}

export default MyDatepickerComponent;
