import React, { useState } from "react";
import api from "../api";

const Users = () => {
  const [users, setUsers] = useState(api.users.fetchAll());
  const sum = 19;
  const node = [];

  let summ = [];

  users.map((user) => {
    return node.push(user.profession.name);
  });
  function forNode(node) {
    node.forEach((el) => {
      // let summ = [];
      // let e = "////////////";
      // console.log(e);
      //onst nodeLength = node.length;
      //console.log("nodeLength", nodeLength);
      let s = el.split("");
      //console.log("word.length: ", s.length);
      s.splice(s.length - 3);
      let join = s.join("");
      let payment = Number(join);
      ///////
      summ.push(payment);
      //console.log("summ: ", summ);
      ///////
      //payment *= 25;

      //console.log("payment is: ", typeof payment);
      //console.log("payment: ", payment);
      //console.log("join: ", join);
      //console.log("");
      return console.log("Node loop is working...!");
    });
  }
  forNode(node);
  /////
  // let er = "//////////";
  //console.log(er);

  //console.log("Elements in summ: ", summ);

  ///

  function arraySum(array) {
    var sum = 0;
    for (var i = 0; i < array.length; i++) {
      sum += array[i];
    }
    //console.log("SUUMMMM IISS: ", sum);
    return sum;
  }
  let paymentSum = arraySum(summ);

  /////
  // console.log("node", node);

  return (
    <React.Fragment>
      <h4>   : {paymentSum}</h4>
      {users.length > 0 && (
        <table className="table">
          <thead>
            <tr>
              <th scope="col"></th>

              <th scope="col"></th>

              <th />
            </tr>
          </thead>
          <tbody>
            {users.map((user) => (
              <tr key={user.id}>
                <td>{user.name}</td>

                <td>
                  {user.profession.name}

                  {/* console.log({money}) */}
                </td>

                <td></td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </React.Fragment>
  );
};

export default Users;
